.App {
	display: flex;
	position: relative;
	justify-content: center;
	align-items: center;
	font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
	font-weight: 400;
	font-size: 1rem;
	line-height: 1.4375em;
	letter-spacing: 0.00938em;
	/* height: 100%; */
	width: 100%;
	padding: 0;
}

p {
	margin-block-start: 0;
	margin-block-end: 0;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
	font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
	margin: 0;
	padding: 0;
	line-height: 1.4375em;
	letter-spacing: 0.00938em;
	height: 100%;
	width: 100%;
	padding: 0;
	word-break: keep-all;
}

.container {
	position: relative;
	min-height: 50%;
	width: 90%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.text {
	min-width: 500px;
	width: 500px;
}

#outlined-textarea {
	min-height: 300px;
}

.result {
	box-sizing: border-box;
	min-height: 345px;
	min-width: 500px;
	width: 500px;
	word-break: break-all;
	border-radius: 4px;
	color: rgba(0, 0, 0, 0.87);
	border: 1px solid #c4c4c4;
	padding: 10px;
	padding-bottom: 30px;
	padding-top: 15px;
}

.result > p {
	margin: 0;
	padding: 0;
	word-break: keep-all;
	white-space: pre-wrap;
	overflow-wrap: break-word;
}

.btns {
	box-sizing: border-box;
	background-color: rgba(194, 194, 194, 0.28);
	padding: 20px 20px;
	border-radius: 4px;
	box-shadow: 0px 0px 24px 2px rgba(34, 60, 80, 0.1);
	max-height: 220px;
	margin: 0 20px;
}

@media (min-width: 1850px) {
	.container {
		width: 70%;
	}
}

@media (min-width: 1450px) and (max-width: 1850px) {
	.container {
		width: 80%;
	}
}

@media (max-width: 1600px) {
	.container {
		min-height: 100%;
		display: flex;
		flex-direction: column;
	}
	.text {
		margin-top: 50px;
		margin-bottom: 30px;
		min-width: 600px;
		width: 600px;
	}
	.btns {
		margin-bottom: 30px;
	}
	.result {
		margin-bottom: 50px;
		min-width: 600px;
		width: 600px;
	}
}

@media (max-width: 660px) {
	.container {
		min-height: 100%;
		display: flex;
		flex-direction: column;
		padding: 0 20px;
	}
	.text {
		margin-top: 30px;
		margin-bottom: 20px;
		min-width: 500px;
		width: 500px;
	}
	.btns {
		margin-bottom: 20px;
	}
	.result {
		margin-bottom: 50px;
		min-width: 500px;
		width: 500px;
	}
}

@media (max-width: 570px) {
	.container {
		min-height: 100%;
		display: flex;
		flex-direction: column;
		padding: 0 20px;
	}
	.text {
		margin-bottom: 20px;
		min-width: 400px;
		width: 400px;
	}
	.btns {
		margin-bottom: 20px;
	}
	.result {
		margin-bottom: 50px;
		min-width: 400px;
		width: 400px;
	}
	#root > div > div > div.btns > div > button {
		font-size: 12px;
	}
	p,
	#root > div > div > div.text > div > div > div > div {
		font-size: 14px;
	}
}

@media (max-width: 460px) {
	.container {
		min-height: 100%;
		display: flex;
		flex-direction: column;
		padding: 0 20px;
	}
	.text {
		margin-bottom: 20px;
		min-width: 350px;
		width: 350px;
	}
	.btns {
		margin-bottom: 20px;
	}
	.result {
		margin-bottom: 50px;
		min-width: 350px;
		width: 350px;
	}
}
